<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="arr in blog.arrs">{{arr}}</li>
    </ul>
    <button v-on:click="deleteSingleBlog()">删除</button>
    <router-link :to="'/edit/'+id">编辑</router-link>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },

  created() {
    this.$http
      .get("https://my-blog-404.firebaseio.com/posts/" + this.id + ".json")
      .then(function(data) {
        // this.blog = data.body;
        return data.json();
      })
      .then(function(data) {
        console.log(data);
        this.blog = data;
      });
  },
  methods: {
    deleteSingleBlog: function() {
      this.$http
        .delete("https://my-blog-404.firebaseio.com/posts/" + this.id + ".json")
        .then(response => {
          this.$router.push({ path: "/" });
        });
    }
  }
};
</script>

<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted black;
}
button,a {
  margin: 20px 0;
  background: crimson;
  border: 0;
  padding: 10px;
  border-radius: 4px;
  font-size: 15px;
  color: white;
  cursor: pointer;
}
a {
  text-decoration: none;
}
</style>





