<template>
  <div id="edit-blog">
    <h2>添加博客</h2>
    <form v-if="!submmite">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkBoxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.arrs">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.arrs">
        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.arrs">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.arrs">
      </div>
      <div>
        <label>作者:</label>
        <select v-model="blog.author">
          <option v-for="author in authors">{{author}}</option>
        </select>
      </div>
      <button v-on:click.prevent="post">更改博客</button>
    </form>

    <hr>
    <div v-if="submmite">
      <h3>您的博客发布成功！</h3>
    </div>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="arr in blog.arrs">{{arr}}</li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "edit-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      authors: ["OuHK", "YJ"],
      submmite: false
    };
  },
  methods: {
    fetchData: function() {
        this.$http.get("https://my-blog-404.firebaseio.com/posts/" + this.id + ".json")
        .then(response=>{
            this.blog = response.body;
        })
    },
    post: function() {
      this.$http
        .put("https://my-blog-404.firebaseio.com/posts/" + this.id + ".json", this.blog)
        .then(function(data) {
          console.log(data);
          this.submmite = true;
        });
    }
  },
  created() {
    this.fetchData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#edit-blog * {
  box-sizing: border-box;
}
#edit-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  background: #ccc;
}
textarea {
  height: 200px;
}
label {
  display: block;
}
input[type="text"],
select,
textarea {
  display: block;
  width: 100%;
  padding: 8px;
}
#checkBoxes {
  margin: 10px 0;
}
#checkBoxes label {
  display: inline-block;
  margin-top: 0;
}
#checkBoxes input {
  display: inline-block;
  margin-right: 10px;
}
button {
  display: block;
  margin: 20px 0;
  background: crimson;
  border: 0;
  padding: 10px;
  border-radius: 4px;
  font-size: 15px;
  color: white;
  cursor: pointer;
}
#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3 {
  margin-top: 10px;
}
</style>



